import { memo, useState } from "react";
import { Cell, Pie, PieChart, ResponsiveContainer, Sector } from "recharts";
import { useAnalytics } from "../../../../contexts/AnalyticsProvider";
import EditControl from "../../EditControl/EditControl";
import classes from "./PieChart.module.css";

const renderActiveShape = (props, chartData) => {
  // const RADIAN = Math.PI / 180;
  const {
    cx,
    cy,
    // midAngle,
    innerRadius,
    outerRadius,
    startAngle,
    endAngle,
    payload,
    // percent,
    value,
  } = props;
  // const sin = Math.sin(-RADIAN * midAngle);
  // const cos = Math.cos(-RADIAN * midAngle);
  // const sx = cx + (outerRadius + 10) * cos;
  // const sy = cy + (outerRadius + 10) * sin;
  // const mx = cx + (outerRadius + 30) * cos;
  // const my = cy + (outerRadius + 30) * sin;
  // const ex = mx + (cos >= 0 ? 1 : -1) * 22;
  // const ey = my;
  // const textAnchor = cos >= 0 ? "start" : "end";
  const label = chartData.labels[payload.name];
  const fill = label.fill;
  return (
    <g>
      <text x={cx} y={cy} dy={0} fontSize="14" textAnchor="middle" fill={fill}>
        {payload.name.toUpperCase()}
      </text>
      <text x={cx} y={cy} dy={25} fontSize="14" textAnchor="middle" fill={fill}>
        {value}
      </text>
      <Sector
        cx={cx}
        cy={cy}
        innerRadius={innerRadius}
        outerRadius={outerRadius}
        startAngle={startAngle}
        endAngle={endAngle}
        fill={fill}
      />
      <Sector
        cx={cx}
        cy={cy}
        startAngle={startAngle}
        endAngle={endAngle}
        innerRadius={outerRadius + 6}
        outerRadius={outerRadius + 10}
        fill={fill}
      />
    </g>
  );
};

const PieChartContainer = ({
  chartData,
  title,
  period,
  editChart,
  deleteChart,
}) => {
  const [activeIndex, setActiveIndex] = useState(0);

  const onPieEnter = (_, index) => {
    setActiveIndex(index);
  };

  const { state, setDrawers } = useAnalytics();

  const { editInstance } = state;

  return (
    <div className={classes.Container}>
      {editInstance && deleteChart && (
        <EditControl
          openDrawer={() =>
            setDrawers((prevState) => ({
              ...prevState,
              openCreatePieChartDrawer: true,
            }))
          }
          editChart={editChart}
          deleteChart={deleteChart}
        />
      )}

      <div
        style={{
          height: editInstance && deleteChart ? "calc(100% - 32px)" : "100%",
        }}
        className={classes.Inner}
      >
        <div className={classes.Chart_Title}>
          <h1>{title}</h1>
          <p>{period === 0 ? "Today" : `${period} days`} </p>
        </div>

        <div className={classes.Chart_Container}>
          <ResponsiveContainer>
            <PieChart>
              <Pie
                activeIndex={activeIndex}
                activeShape={(props) => renderActiveShape(props, chartData)}
                data={chartData.dataSet.data}
                cx="50%"
                cy="50%"
                innerRadius={60}
                outerRadius={80}
                dataKey="value"
                onMouseEnter={onPieEnter}
              >
                {chartData.dataSet.data.map((entry, index) => (
                  <Cell
                    key={`cell-${index}`}
                    fill={chartData.dataSet.backgroundColor[index]}
                  />
                ))}
              </Pie>
            </PieChart>
          </ResponsiveContainer>
        </div>
      </div>
    </div>
  );
};

export default memo(PieChartContainer);
