import { Chip } from "@material-ui/core";
import { useState } from "react";
import styled from "styled-components";
import { panes } from "../../../Constants/Analytics";

const Container = styled.div`
  width: 100%;

  margin-top: 5%;

  & > div {
    display: flex;
    justify-content: space-around;
  }
`;

const Title = styled.h1`
  font-size: 18px;
  font-weight: 500;

  margin-left: 18px;
  margin-bottom: 15px;
`;

const ChartDurationWithChip = ({ title, handleChange, value }) => {
  const [selectedTabIdx, setSelectedTabIdx] = useState(value);

  return (
    <Container>
      <Title>{title}</Title>
      <div>
        {panes.map(({ menuItem, value }, i) => {
          return (
            <Chip
              key={i}
              style={
                value === selectedTabIdx
                  ? {
                      background: "black",
                      color: "white",
                      border: "1px solid #F2F2F2",
                    }
                  : {
                      color: "black",
                      background: "white",
                      border: "1px solid #F2F2F2",
                    }
              }
              label={menuItem}
              clickable
              onClick={() => {
                handleChange(value);
                setSelectedTabIdx(value);
              }}
            />
          );
        })}
      </div>
    </Container>
  );
};

export default ChartDurationWithChip;
