import {
  Divider,
  Drawer,
  List,
  ListItem,
  ListItemSecondaryAction,
  ListItemText,
  withStyles,
} from "@material-ui/core";
import { Fragment, useState } from "react";
import { useAnalytics } from "../../../contexts/AnalyticsProvider";
import Modal2 from "../../UI/Modal2/Modal2";
import classes from "./AddChartDrawer.module.css";

const CustomDrawer = withStyles((theme) => ({
  paper: { width: "100%", maxWidth: 400 },
}))(Drawer);

const AddChartDrawer = ({ open, handleDrawerToggle }) => {
  const { setDrawers } = useAnalytics();

  const [options] = useState([
    {
      label: "Line Chart",
      icon: "https://storage.googleapis.com/easysocial/Line-Chart.png",
      onClick: () =>
        setDrawers((prevState) => ({
          ...prevState,
          openCreateLineChartDrawer: true,
        })),
    },
    {
      label: "Pie Chart",
      icon: "https://storage.googleapis.com/easysocial/pie-chart.png",
      onClick: () =>
        setDrawers((prevState) => ({
          ...prevState,
          openCreatePieChartDrawer: true,
        })),
    },
    {
      label: "Display Feed",
      icon: "https://storage.googleapis.com/easysocial/display-feed.png",
      onClick: () =>
        setDrawers((prevState) => ({
          ...prevState,
          openCreateDisplayFeedDrawer: true,
        })),
    },
    {
      label: "Performance Table",
      icon: "https://storage.googleapis.com/easysocial/Performance%20table.png",
      onClick: () =>
        setDrawers((prevState) => ({
          ...prevState,
          openCreatePerformanceTableDrawer: true,
        })),
    },
  ]);
  const container =
    window !== undefined ? () => window.document.body : undefined;

  return (
    <CustomDrawer
      container={container}
      variant="temporary"
      anchor="right"
      open={open}
      onClose={handleDrawerToggle}
      classes={{}}
      ModalProps={{
        keepMounted: true, // Better open performance on mobile.
      }}
    >
      <Modal2 title="Select chart type" close={handleDrawerToggle} />

      <Divider style={{ width: "90%", margin: "0 auto" }} />

      <List classes={{ root: classes.List }}>
        {options.map(({ label, icon, onClick }, i) => {
          return (
            <Fragment key={i}>
              <ListItem
                key={i}
                button
                classes={{ root: classes.ListItem }}
                onClick={onClick}
              >
                <ListItemText primary={label} />
                <ListItemSecondaryAction>
                  <img style={{ opacity: 0.7 }} src={icon} alt={label} />
                </ListItemSecondaryAction>
              </ListItem>
              <Divider key={i + 1} />
            </Fragment>
          );
        })}
      </List>
    </CustomDrawer>
  );
};

export default AddChartDrawer;
