import React from "react";
import { useAnalytics } from "../../../contexts/AnalyticsProvider";
import GraphBlock from "./GraphBlock/GraphBlock";

const GridItemContainer = React.forwardRef(
  (
    {
      children,
      item,
      chartItems,
      style,
      classes,
      layout,
      setLayout,
      editInstance,
      ...props
    },
    ref
  ) => {
    const { workspaceData, deleteChart, editChart } = useAnalytics();

    const deleteChartItem = () => {
      if (!editInstance) {
        return;
      }
      const newLayout = layout;
      const itemToRemove = chartItems[item].itemKey;
      for (const layoutItems of Object.keys(newLayout)) {
        const index = newLayout[layoutItems].findIndex(
          (e) => e.i === itemToRemove
        );
        if (index !== -1) {
          newLayout[layoutItems].splice(index, 1);
        }
      }
      deleteChart(chartItems[item], newLayout);
    };

    if (!workspaceData[item]) return <></>;
    return (
      <div className="grid-item__title" ref={ref} style={style} {...props}>
        <GraphBlock
          deleteChart={deleteChartItem}
          editChart={() => editChart(chartItems[item])}
          title={chartItems[item].title}
          type={chartItems[item].type}
          data={workspaceData[item]}
          period={chartItems[item].period}
          workspaceData={workspaceData[item]}
          chartItem={chartItems[item]}
          {...props}
        />
        {children}
      </div>
    );
  }
);

export default GridItemContainer;
