import { memo, useRef } from "react";
import LineChart from "../../Charts/LineChart/LineChart";
import DisplayFeed from "../../Charts/DisplayFeed/DisplayFeed";
import PerformanceTable from "../../Charts//PerformanceTable/PerformanceTable";
import PieChart from "../../Charts/PieChart/PieChart";
import classes from "./GraphBlock.module.css";
import clsx from "clsx";
import EditControl from "../../EditControl/EditControl";
import { useAnalytics } from "../../../../contexts/AnalyticsProvider";

const GraphBlock = ({
  type,
  data,
  width,
  height,
  period,
  title,
  chartItem,
  workspaceData,
  deleteChart,
  editChart,
}) => {
  // useEffect(() => {
  //   console.log("render GraphBlock - useEffect");
  // }, []);

  // console.log("render GraphBlock - No useEffect");
  const feedRef = useRef();

  const { state, setDrawers, memberUserId, userRole, currentWorkspaceData } =
    useAnalytics();

  if (!data) {
    return <></>;
  }

  switch (type) {
    case "line":
      return (
        <LineChart
          chartData={data}
          title={title}
          period={period}
          deleteChart={deleteChart}
          editChart={editChart}
        />
      );
    case "pie": {
      return (
        <PieChart
          deleteChart={deleteChart}
          editChart={editChart}
          chartData={data}
          title={title}
          period={period}
        />
      );
    }
    case "feed":
      return (
        <div
          ref={feedRef}
          style={{ paddingTop: state.editInstance ? "0px" : "10px" }}
          className={clsx("feed", classes.DisplayFeed)}
        >
          {state.editInstance && (
            <EditControl
              editChart={editChart}
              deleteChart={deleteChart}
              openDrawer={() =>
                setDrawers((prevState) => ({
                  ...prevState,
                  openCreateDisplayFeedDrawer: true,
                }))
              }
            />
          )}
          <div className={classes.Chart_Title}>
            <h1>{title || "Live feed"}</h1>
            <p>{period === 0 ? "Today" : `${period} days`} </p>
          </div>
          <div
            style={{
              height: state.editInstance
                ? "calc(100% - 53px)"
                : "calc(100% - 30px)",
            }}
            className={clsx(
              classes.FeedCards,
              chartItem.alignment === "verticle"
                ? classes.Vertical
                : feedRef.current &&
                  feedRef.current.scrollWidth > feedRef.current.offsetWidth
                ? "justify-content-start"
                : "justify-content-around"
            )}
          >
            {chartItem.feedItems.map((f, idx) => {
              let data = workspaceData[idx];
              return (
                <DisplayFeed
                  itemType={chartItem.itemType}
                  param={chartItem.param}
                  key={"DI" + idx}
                  {...data}
                  userId={memberUserId}
                  userRole={userRole}
                  fLabel={f.label}
                  visibility={currentWorkspaceData.visibiltyGroup}
                />
              );
            })}
          </div>
        </div>
      );
    case "table":
      return (
        <PerformanceTable
          deleteChart={deleteChart}
          editChart={editChart}
          title={title}
          period={period}
          data={data}
          rows={chartItem.rows || 10}
          sorting={chartItem?.sorting}
          column1={chartItem.label}
          column2={"Leads"}
          param={chartItem.param}
          itemType={chartItem.itemType}
          userId={memberUserId}
          userRole={userRole}
          visibility={currentWorkspaceData.visibiltyGroup}
        />
      );
    default:
      return null;
  }
};

export default memo(GraphBlock);
