import {
  Container,
  Divider,
  Drawer,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  withStyles,
  TextField,
} from "@material-ui/core";
import React, { useEffect, useState } from "react";
import styled from "styled-components";
import {
  availableSorting,
  fakePreviewData,
} from "../../../../Constants/Analytics";
import { useAnalytics } from "../../../../contexts/AnalyticsProvider";
import Modal2 from "../../../UI/Modal2/Modal2";
import ChartDurationWithChip from "../../ChartDurationWithChip/ChartDurationWithChip";
import classes from "./CreatePerformanceTableDrawer.module.css";
import { v4 as uuid } from "uuid";
import PerformanceTable from "../../Charts/PerformanceTable/PerformanceTable";
import CreateChartButton from "../../CreateChartButton/CreateChartButton";
import { useToastMessage } from "../../../../contexts/ToastMessageContext";

const OuterContainer = styled.div`
  width: 100%;
  max-width: 500px;

  margin-bottom: 20px;
`;

const CustomDrawer = withStyles((theme) => ({
  paper: {
    width: "100%",
    maxWidth: "400px",
  },
}))(Drawer);

const CustomSelect = withStyles(() => ({
  root: {
    backgroundColor: "#f2f3f7",
  },
}))(Select);

const SelectParameterType = ({
  selectedParameter,
  handleChangeParameterType,
  dataFields,
}) => (
  <FormControl variant="filled" className={classes.ItemSelect}>
    <InputLabel id="demo-simple-select-filled-label">Parameter Type</InputLabel>
    <CustomSelect
      labelId="demo-simple-select-filled-label"
      id="demo-simple-select-filled"
      value={selectedParameter || " "}
      onChange={handleChangeParameterType}
    >
      <MenuItem value="">
        <em>None</em>
      </MenuItem>
      {dataFields &&
        dataFields.map((parameter, i) => {
          if (parameter.field_options.length > 0) {
            return (
              <MenuItem
                value={parameter.field_name}
                classes={{ root: classes.MenuItem }}
                key={i}
              >
                {parameter.display_name}
              </MenuItem>
            );
          }
          return null;
        })}
    </CustomSelect>
  </FormControl>
);

const CreatePerformanceTableDrawer = ({
  open,
  handleDrawerToggle,
  keepMounted,
}) => {
  const container =
    window !== undefined ? () => window.document.body : undefined;

  const [selectedItem, setSelectedItem] = useState("parameters");
  const [selectedParameter, setSelectedParameter] = useState(null);
  const [cardItems, setCardItems] = useState([]);
  const [, setReload] = useState(false);
  const [chartDuration, setChartDuration] = useState(7);

  const [chartTitle, setChartTitle] = useState("");

  const [tableOptions, setTableOptions] = useState({
    sorting: availableSorting[0],
    rows: 10,
    title: undefined,
    label: undefined,
  });

  const { setErrorMessage } = useToastMessage();
  const {
    state,
    dataFields,
    createChartService,
    currentWorkspace,
    workspaces,
    editChartService,
  } = useAnalytics();

  const [isDepartment, setDepartment] = useState(false);
  const [isTeam, setTeam] = useState(false);

  const { editChart, editParams, editInstance } = state;

  useEffect(() => {
    if (open) {
      if (editChart && editParams && editInstance) {
        if (editParams.type === "table") {
          setSelectedItem(editParams.itemType);
          setSelectedParameter(editParams.param);

          editParams.lineItems &&
            editParams.lineItems.forEach((e, i) => {
              delete e._id;
            });

          editParams.lineItems && setCardItems(editParams.lineItems);

          setChartDuration(editParams.period);
          setChartTitle(editParams.title);

          setTableOptions({
            ...tableOptions,
            rows: editParams.rows,
            label: editParams.label,
            sorting: editParams.sorting,
            title: editParams.title,
          });
        }
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [open]);

  useEffect(() => {
    if (workspaces) {
      const current = workspaces.find((e) => e._id === currentWorkspace);

      if (current) {
        if (current.visibiltyGroup === "departments") {
          setDepartment(true);
        } else if (current.visibiltyGroup === "team heads") {
          setTeam(true);
        }
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentWorkspace]);

  const _setRows = (value) => {
    let table = tableOptions;
    table.rows = value;
    setTableOptions(table);
    setReload(uuid());
  };

  const _setLabel = (value) => {
    let table = tableOptions;
    table.label = value;

    setTableOptions(table);
  };

  const _setSorting = (value) => {
    let table = tableOptions;
    table.sorting = value;
    setTableOptions(table);

    setReload(uuid());
  };

  const handleChange = (event) => {
    setSelectedItem(event.target.value);

    setCardItems([]);
  };

  const handleChangeParameterType = (event) => {
    setSelectedParameter(event.target.value);

    setCardItems([]);
  };

  const _createChart = () => {
    const current = workspaces.find((e) => e._id === currentWorkspace);

    if ((!chartTitle || chartTitle.length <= 1) && cardItems.length < 1) {
      return setErrorMessage("Chart name cannot be empty!");
    }

    handleDrawerToggle();

    const obj = {
      type: "table",
      duration: chartDuration,
      comparison: false,
      param: selectedParameter,
      itemType: selectedItem,
      lineItems: cardItems,
      layoutSize: 0,
      alignment: "horizontal",
      pTableOptions: {
        rows: tableOptions.rows,
        label: tableOptions.label,
        sorting: tableOptions.sorting,
        title: chartTitle,
      },
    };

    if (editChart && editInstance) {
      return editChartService(
        {
          params: { ...obj, itemKey: editParams.itemKey },
          dashboardId: currentWorkspace,
        },
        current.visibiltyGroup.replace(" ", "").toLowerCase()
      );
    }

    createChartService(
      { params: obj, dashboardId: currentWorkspace },
      current.visibiltyGroup.replace(" ", "").toLowerCase()
    );
  };
  return (
    <CustomDrawer
      container={container}
      variant="temporary"
      anchor="right"
      open={open}
      onClose={handleDrawerToggle}
      classes={{}}
      ModalProps={{
        keepMounted: keepMounted, // Better open performance on mobile.
      }}
    >
      <div className={classes.Container}>
        <OuterContainer>
          <Modal2
            title="Create a Performance Table"
            close={handleDrawerToggle}
          />

          <Divider style={{ width: "90%", margin: "0 auto" }} />

          <Container className="d-flex flex-column pt-3">
            <PerformanceTable {...fakePreviewData.table} />

            <ChartDurationWithChip
              value={chartDuration}
              title="Select Duration"
              handleChange={(value) => setChartDuration(value)}
            />

            {/* Item type */}
            <FormControl variant="filled" className={classes.ItemSelect}>
              <InputLabel id="demo-simple-select-filled-label">
                Item Type
              </InputLabel>
              <CustomSelect
                labelId="demo-simple-select-filled-label"
                id="demo-simple-select-filled"
                value={selectedItem}
                onChange={handleChange}
              >
                <MenuItem value={"parameters"}>Parameters</MenuItem>
                {isDepartment && (
                  <MenuItem value={"teamLeads"}>Team Leads</MenuItem>
                )}
                {isTeam && <MenuItem value={"userLead"}>User Leads</MenuItem>}
              </CustomSelect>
            </FormControl>

            {/* Select parameter type */}
            {selectedItem === "parameters" && (
              <SelectParameterType
                dataFields={dataFields}
                selectedParameter={selectedParameter}
                handleChangeParameterType={handleChangeParameterType}
              />
            )}

            {isDepartment && selectedItem === "teamLeads" && (
              <SelectParameterType
                parameters={[
                  {
                    field_name: "teamLeads",
                    field_options: [],
                    display_name: "Team Leads",
                  },
                ]}
                selectedParameter={selectedParameter}
                handleChangeParameterType={handleChangeParameterType}
              />
            )}
            {isTeam && selectedItem === "userLead" && (
              <SelectParameterType
                parameters={[
                  {
                    field_name: "userLead",
                    field_options: [],
                    display_name: "User Lead",
                  },
                ]}
                selectedParameter={selectedParameter}
                handleChangeParameterType={handleChangeParameterType}
              />
            )}

            {/* Items to display */}
            <FormControl variant="filled" className={classes.ItemSelect}>
              <InputLabel id="demo-simple-select-filled-label">
                Items to display
              </InputLabel>
              <CustomSelect
                labelId="demo-simple-select-filled-label"
                id="demo-simple-select-filled"
                value={tableOptions.rows}
                onChange={(e) => _setRows(e.target.value)}
              >
                {["5", "10", "15", "25", "50"].map((num, i) => (
                  <MenuItem value={num} key={i}>
                    {num}
                  </MenuItem>
                ))}
              </CustomSelect>
            </FormControl>

            {/* SORTING */}
            <FormControl variant="filled" className={classes.ItemSelect}>
              <InputLabel id="demo-simple-select-filled-label">
                Sorting
              </InputLabel>
              <CustomSelect
                labelId="demo-simple-select-filled-label"
                id="demo-simple-select-filled"
                value={tableOptions.sorting}
                onChange={(e) => _setSorting(e.target.value)}
              >
                {availableSorting.map((sorting, i) => (
                  <MenuItem value={sorting} key={i}>
                    {sorting}
                  </MenuItem>
                ))}
              </CustomSelect>
            </FormControl>

            {/* Label */}
            <FormControl variant="filled" className={classes.ItemSelect}>
              <TextField
                id="filled-basic"
                label="Label"
                variant="filled"
                className={classes.Input}
                onChange={(e) => _setLabel(e.target.value)}
                type="text"
                defaultValue={tableOptions.label}
              />
            </FormControl>

            <CreateChartButton
              style={{ marginTop: "10px" }}
              value={chartTitle}
              onChange={(value) => setChartTitle(value)}
              onClick={_createChart}
              text={editInstance && "Update"}
            />
          </Container>
        </OuterContainer>
      </div>
    </CustomDrawer>
  );
};

export default CreatePerformanceTableDrawer;
