import { Card, Image } from "semantic-ui-react";
import ProfitIcon from "../../../../assets/icons/profit.svg";
import LossIcon from "../../../../assets/icons/loss.svg";
import classes from "./DisplayFeed.module.css";
import { bool, string } from "prop-types";
import { number } from "prop-types";
import { any } from "prop-types";
import { object } from "prop-types";
import moment from "moment";
import { Button } from "@material-ui/core";

const DisplayFeed = ({
  profit,
  period,
  label,
  count,
  style,
  param,
  itemType,
  fLabel,
  userId,
  userRole,
  visibility,
  demoChart = false,
  ...rest
}) => {
  const fromDate = moment()
    .subtract(period, "d")
    .hours(0)
    .minutes(0)
    .format("YYYY-MM-DDTHH:mm");

  const toDate = moment().format("YYYY-MM-DDTHH:mm");

  let redirectLink = "";

  if (!demoChart) {
    if (itemType === "parameters") {
      if (userRole === "admin" && visibility === "team members") {
        redirectLink = `/engage/chat?from=${fromDate}&to=${toDate}&${param}=${label}&assignedTo=${userId}#All`;
      } else {
        redirectLink = `/engage/chat?from=${fromDate}&to=${toDate}&${param}=${label}#All`;
      }
    } else {
      if (userRole === "admin" && visibility === "team members") {
        redirectLink = `/engage/chat?from=${fromDate}&to=${toDate}&assignedTo=${userId}#${label}`;
      } else {
        redirectLink = `/engage/chat?from=${fromDate}&to=${toDate}#${label}`;
      }
    }
  }

  return (
    <>
      <Card className={classes.Container} {...rest} style={style}>
        <Button
          className={classes.Card_Button}
          onClick={() => !demoChart && window.open(redirectLink, "_blank")}
        >
          <div
            style={{
              marginBottom: 0,
              background: profit
                ? "rgba(84, 255, 101,0.1)"
                : "rgba(220,20,60,0.1)",
              marginRight: 10,
              display: "flex",
              alignItems: "center",
              justifyContent: "space-evenly",
              padding: "4px",
              borderRadius: 12,
            }}
          >
            <Image
              src={profit ? ProfitIcon : LossIcon}
              className="p-1"
              style={{ width: "35px", height: "35px" }}
            />
          </div>
          <div className={classes.Container_Txt}>
            <div
              className="d-flex align-items-baseline"
              style={{ fontSize: 18, fontWeight: 600 }}
            >
              <h4>{count}</h4>
            </div>
            <Card.Meta
              style={{ opacity: 0.5, color: "#24272C", fontSize: "13px" }}
            >
              {`${fLabel}`}
            </Card.Meta>
          </div>
        </Button>
      </Card>
    </>
  );
};

DisplayFeed.propTypes = {
  profit: bool.isRequired,
  period: number.isRequired,
  label: string.isRequired,
  count: any.isRequired,
  style: object,
  fLabel: string.isRequired,
  itemType: string.isRequired,
  param: string.isRequired,
};

export default DisplayFeed;
