import { Chip } from "@material-ui/core";
import React from "react";
import styled from "styled-components";
import { alignMent } from "../../../Constants/Analytics";

const Container = styled.div`
  width: max-content;

  margin-top: 5%;

  & > div {
    display: flex;
    justify-content: space-around;

    & > div {
      margin-right: 10px;
    }
  }
`;

const Title = styled.h1`
  font-size: 18px;
  font-weight: 500;

  margin-left: 5px;
  margin-bottom: 15px;
`;

const ChartAlignmentChip = ({ title, handleChange, value }) => {
  return (
    <Container>
      <Title>{title}</Title>
      <div>
        {alignMent.map(({ item }, i) => {
          return (
            <Chip
              key={i}
              style={
                value === item
                  ? {
                      background: "black",
                      color: "white",
                      border: "1px solid #F2F2F2",
                    }
                  : {
                      color: "black",
                      background: "white",
                      border: "1px solid #F2F2F2",
                    }
              }
              label={item}
              clickable
              onClick={() => {
                handleChange(item);
              }}
            />
          );
        })}
      </div>
    </Container>
  );
};

export default ChartAlignmentChip;
