import {
  List,
  ListItem,
  ListItemSecondaryAction,
  ListItemText,
} from "@material-ui/core";
import moment from "moment";
import React, { memo, useEffect, useState } from "react";
import { Card } from "semantic-ui-react";
import { useAnalytics } from "../../../../contexts/AnalyticsProvider";
import EditControl from "../../EditControl/EditControl";
import classes from "./PerformanceTable.module.css";

const PerformanceTable = ({
  title = "untitled",
  period,
  deleteChart,
  editChart,
  userRole,
  userId,
  visibility,
  itemType,
  param,
  ...props
}) => {
  const [data, setData] = useState([]);

  useEffect(() => {
    setData(sortData(props.sorting));
  }, [props]);

  const sortMethodAlphaNum = (data, key, dir) => {
    return data.sort(function (a, b) {
      let textA = a[key].toUpperCase();
      let textB = b[key].toUpperCase();
      if (dir === "ascending") {
        return textA.localeCompare(textB);
      } else {
        return textB.localeCompare(textA);
      }
    });
  };

  const sortMethodNum = (data, key, dir) => {
    return data.sort(function (a, b) {
      let A = a[key];
      let B = b[key];
      if (dir === "ascending") {
        return A - B;
      } else {
        return B - A;
      }
    });
  };

  const sortData = (sorting) => {
    if (!sorting || !props.data) {
      return props.data;
    }
    if (sorting === "Alphabatical" || sorting === "Reverse-Alphabatical") {
      if (sorting === "Alphabatical") {
        return sortMethodAlphaNum(props.data, "key", "ascending");
      } else {
        return sortMethodAlphaNum(props.data, "key", "descending");
      }
    } else {
      if (sorting === "Low-to-High") {
        return sortMethodNum(props.data, "value", "ascending");
      } else {
        return sortMethodNum(props.data, "value", "descending");
      }
    }
  };

  const { state, setDrawers } = useAnalytics();

  const { editInstance } = state;

  const fromDate = moment()
    .subtract(period, "d")
    .hours(0)
    .minutes(0)
    .format("YYYY-MM-DDTHH:mm");

  const toDate = moment().format("YYYY-MM-DDTHH:mm");

  return (
    <>
      <Card className={classes.Container}>
        {editInstance && deleteChart && (
          <EditControl
            openDrawer={() =>
              setDrawers((prevState) => ({
                ...prevState,
                openCreatePerformanceTableDrawer: true,
              }))
            }
            editChart={editChart}
            deleteChart={deleteChart}
          />
        )}

        <div
          style={{
            height: editInstance && deleteChart ? "calc(100% - 32px)" : "100%",
          }}
          className={classes.Inner}
        >
          <div className={classes.Chart_Title}>
            <h1>{title || "Title"}</h1>
            <p>{period === 0 ? "Today" : `${period || 30} days`} </p>
          </div>
          <List classes={{ root: classes.List }}>
            {data.map((el, idx) => {
              return (
                <ListItem
                  onClick={() => {
                    let redirectLink = "";

                    if (itemType === "parameters") {
                      if (
                        userRole === "admin" &&
                        visibility === "team members"
                      ) {
                        redirectLink = `/dashboard?from=${fromDate}&to=${toDate}&${param}=${el.key}&assignedTo=${userId}`;
                      } else {
                        redirectLink = `/dashboard?from=${fromDate}&to=${toDate}&${param}=${el.key}`;
                      }

                      return window.open(redirectLink, "_blank");
                    }
                  }}
                  button={itemType === "parameters"}
                  className={classes.Row}
                  key={"pTableRow" + idx}
                  style={{
                    display: "flex",
                    justifyContent: "space-between",
                  }}
                >
                  <ListItemText
                    classes={{
                      primary: classes.PrimaryText,
                      secondary: classes.SecondaryText,
                    }}
                    primary={el.key}
                  />

                  <ListItemSecondaryAction style={{ paddingRight: 0 }}>
                    {el.value}
                  </ListItemSecondaryAction>
                  {/* <span>{el.key}</span>
                    <span>{el.value}</span> */}
                </ListItem>
              );
            })}
          </List>
        </div>
      </Card>
    </>
  );
};

export default memo(PerformanceTable);
