import { IconButton } from "@material-ui/core";
import { Close, Create, Reorder } from "@material-ui/icons";
import clsx from "clsx";
import React, { memo } from "react";
import classes from "./EditControl.module.css";

const EditControl = ({ editChart, deleteChart, openDrawer }) => {
  return (
    <div className={classes.Container}>
      <div className={clsx(classes.inner, classes.Inner1)}>
        <IconButton className="drag__handler">
          <Reorder />
        </IconButton>
      </div>
      <div className={clsx(classes.inner, classes.Inner2)}>
        <IconButton
          onClick={() => {
            editChart();
            openDrawer();
          }}
        >
          <Create />
        </IconButton>
        <IconButton onClick={deleteChart}>
          <Close />
        </IconButton>
      </div>
    </div>
  );
};

export default memo(EditControl);
