import React from "react";
import { WidthProvider, Responsive } from "react-grid-layout";

import "./grid-layout.css";
import { useState } from "react";
import { useEffect } from "react";
import classes from "./GridContainer.module.css";
import GridItemContainer from "../../../Components/Analytics/GridItemContainer/GriditemContainer";
import { useCallback } from "react";
import clsx from "clsx";
const ReactGridLayout = WidthProvider(Responsive);

const GridContainer = ({
  instance,
  updateLayout,
  editInstance,
  workspaceData,
}) => {
  const [items, setItems] = useState(null);
  const [layout, setLayout] = useState([]);

  const [, setCurrentBreakpoint] = useState("lg");

  const createItemObj = useCallback(() => {
    let obj = {};
    instance.charts.forEach((ch, idx) => {
      obj[ch.itemKey] = ch;
    });
    instance.feeds.forEach((f, idx) => {
      obj[f.itemKey] = f;
    });
    instance.tables.forEach((t, idx) => {
      obj[t.itemKey] = t;
    });

    setItems(obj);
  }, [instance.charts, instance.feeds, instance.tables]);

  useEffect(() => {
    if (instance && instance.layout) {
      setItems(null);
      setLayout(instance.layout);
    }

    createItemObj();
  }, [instance, createItemObj]);

  useEffect(() => {
    createItemObj();
  }, [createItemObj]);

  const ref = React.createRef();

  return (
    <>
      {items && workspaceData && (
        <ReactGridLayout
          onBreakpointChange={(breakpoint) => setCurrentBreakpoint(breakpoint)}
          className={clsx("layout_custom", editInstance && "onEdit")}
          layouts={layout}
          rowHeight={40}
          cols={{ lg: 12, md: 12, sm: 8, xs: 4, xxs: 2 }}
          onLayoutChange={(layout, allLayout) => {
            if (!editInstance) {
              return;
            }
            setLayout(allLayout);
            updateLayout(allLayout);
          }}
          draggableHandle=".drag__handler"
          isDraggable={editInstance}
          isRearrangeable={editInstance}
          isResizable={editInstance}
          measureBeforeMount
          useCSSTransforms={true}
          breakpoints={{ lg: 1700, md: 900, sm: 580, xs: 375, xxs: 0 }}
        >
          {items &&
            Object.keys(items).map((item) => {
              return (
                <GridItemContainer
                  ref={ref}
                  chartItems={items}
                  item={item}
                  key={item}
                  layout={layout}
                  setLayout={setLayout}
                  classes={classes}
                  editInstance={editInstance}
                />
              );
            })}
        </ReactGridLayout>
      )}
    </>
  );
};

export default GridContainer;
