import {
  Button,
  Container,
  Divider,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  withStyles,
} from "@material-ui/core";
import React, { useEffect, useState } from "react";
import {
  colors,
  fakePreviewData,
  MAX_ITEM,
} from "../../../../Constants/Analytics";
import { useAnalytics } from "../../../../contexts/AnalyticsProvider";
import Modal2 from "../../../UI/Modal2/Modal2";
import ChartDurationWithChip from "../../ChartDurationWithChip/ChartDurationWithChip";
import PieChart from "../../Charts/PieChart/PieChart";
import ItemCard from "../../ItemCard/ItemCard";
import classes from "./CreatePieChartDrawer.module.css";
import { v4 as uuid } from "uuid";
import CreateChartButton from "../../CreateChartButton/CreateChartButton";
import { useToastMessage } from "../../../../contexts/ToastMessageContext";
import { CustomDrawer, OuterContainer } from "../../../../utils/analytics";

const CustomSelect = withStyles(() => ({
  root: {
    backgroundColor: "#f2f3f7",
  },
}))(Select);

const SelectParameterType = ({
  selectedParameter,
  handleChangeParameterType,
  dataFields,
}) => (
  <FormControl variant="filled" className={classes.ItemSelect}>
    <InputLabel id="demo-simple-select-filled-label">Parameter Type</InputLabel>
    <CustomSelect
      labelId="demo-simple-select-filled-label"
      id="demo-simple-select-filled"
      value={selectedParameter || ""}
      onChange={handleChangeParameterType}
    >
      <MenuItem value="">
        <em>None</em>
      </MenuItem>
      {dataFields &&
        dataFields.map((parameter, i) => {
          if (parameter.field_options) {
            return (
              <MenuItem
                value={parameter.field_name}
                classes={{ root: classes.MenuItem }}
                key={i}
              >
                {parameter.field_name}
              </MenuItem>
            );
          }
          return null;
        })}
    </CustomSelect>
  </FormControl>
);

const CreatePieChartDrawer = ({ open, handleDrawerToggle, keepMounted }) => {
  const container =
    window !== undefined ? () => window.document.body : undefined;

  const [selectedItem, setSelectedItem] = useState("parameters");
  const [selectedParameter, setSelectedParameter] = useState(null);
  const [cardItems, setCardItems] = useState([]);
  const [, setReload] = useState(false);
  const [chartDuration, setChartDuration] = useState(7);
  const { setErrorMessage } = useToastMessage();

  const [previousWeek] = useState(false);

  const [chartTitle, setChartTitle] = useState("");

  const {
    state,
    dataFields,
    createChartService,
    currentWorkspace,
    workspaces,
    editChartService,
  } = useAnalytics();

  const { editChart, editParams, editInstance } = state;

  useEffect(() => {
    if (open) {
      if (editChart && editParams && editInstance) {
        if (editParams.type === "pie") {
          setSelectedItem(editParams.itemType);
          setSelectedParameter(editParams.param);

          editParams.lineItems &&
            editParams.lineItems.forEach((e, i) => {
              e.id = e._id;
              delete e._id;
            });

          editParams.lineItems && setCardItems(editParams.lineItems);

          setChartDuration(editParams.period);
          setChartTitle(editParams.title);
        }
      }
    } else {
      setSelectedItem("parameters");
      setSelectedParameter(null);

      setCardItems([]);

      setChartDuration(0);
      setChartTitle("");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [open]);

  const handleChange = (event) => {
    setSelectedParameter(null);
    setSelectedItem(event.target.value);

    setCardItems([]);
  };

  const handleCloseCard = (id) => {
    const temp = cardItems.filter((e) => e.id !== id);

    setCardItems(temp);

    setReload(uuid());
  };

  const handleChangeParameterType = (event) => {
    setSelectedParameter(event.target.value);

    setCardItems([]);
  };

  const _setCardItemInputValue = (idx, value) => {
    let temp = cardItems;

    const index = temp.findIndex((e) => e.id === idx);

    if (index === -1) return;

    temp[index].label = value;

    setCardItems(temp);
  };

  const _setCardItemSelectValue = (idx, value) => {
    let temp = cardItems;

    const index = temp.findIndex((e) => e.id === idx);

    if (index === -1) return;

    temp[index].item = value;

    setCardItems(temp);

    setReload(uuid());
  };

  const _setCardItemColor = (idx, value) => {
    let temp = cardItems;

    const index = temp.findIndex((e) => e.id === idx);

    if (index === -1) return;

    temp[index].color = value;

    setCardItems(temp);
  };

  const _addCard = () => {
    const id = uuid();

    setCardItems((prevState) => {
      prevState.push({
        item: "",
        label: "",
        color: colors[cardItems.length === 0 ? 0 : cardItems.length].color,
        id: id,
      });
      return prevState;
    });

    setReload(id);
  };

  const _createChart = () => {
    if ((!chartTitle || chartTitle.length <= 1) && cardItems.length < 1) {
      return setErrorMessage("Chart name cannot be empty!");
    }

    const obj = {
      type: "pie",
      duration: chartDuration,
      comparison: previousWeek,
      chartTitle: chartTitle,
      param: selectedParameter,
      itemType: selectedItem,
      lineItems: cardItems,
      layoutSize: 0,
      alignment: "horizontal",
      pTableOptions: {
        rows: 10,
      },
    };

    const current = workspaces.find((e) => e._id === currentWorkspace);

    handleDrawerToggle();

    if (editChart) {
      return editChartService(
        {
          params: { ...obj, itemKey: editParams.itemKey },
          dashboardId: currentWorkspace,
        },
        current.visibiltyGroup.replace(" ", "").toLowerCase()
      );
    }

    createChartService(
      { params: obj, dashboardId: currentWorkspace },
      current.visibiltyGroup.replace(" ", "").toLowerCase()
    );
  };

  return (
    <CustomDrawer
      container={container}
      variant="temporary"
      anchor="right"
      open={open}
      onClose={handleDrawerToggle}
      classes={{}}
      ModalProps={{
        keepMounted: keepMounted, // Better open performance on mobile.
      }}
    >
      <div className={classes.Container}>
        <OuterContainer>
          <Modal2 title="Create a pie chart" close={handleDrawerToggle} />

          <Divider style={{ width: "90%", margin: "0 auto" }} />

          <Container className="chart">
            <div className={classes.Chart}>
              <PieChart
                title="Pie Chart"
                period={90}
                chartData={fakePreviewData.piechart}
              />
            </div>

            <ChartDurationWithChip
              value={chartDuration}
              title="Select Duration"
              handleChange={(value) => setChartDuration(value)}
            />

            {/* Item type */}
            <FormControl variant="filled" className={classes.ItemSelect}>
              <InputLabel id="demo-simple-select-filled-label">
                Item Type
              </InputLabel>
              <CustomSelect
                labelId="demo-simple-select-filled-label"
                id="demo-simple-select-filled"
                value={selectedItem}
                onChange={handleChange}
              >
                <MenuItem value="">
                  <em>None</em>
                </MenuItem>
                <MenuItem value={"parameters"}>Parameters</MenuItem>
              </CustomSelect>
            </FormControl>

            {/* Select parameter type */}
            {selectedItem === "parameters" && (
              <SelectParameterType
                dataFields={dataFields}
                selectedParameter={selectedParameter}
                handleChangeParameterType={handleChangeParameterType}
              />
            )}
          </Container>
        </OuterContainer>

        <Divider className={classes.Divider} />
        {/* Item Cards will be placed here  */}
        <OuterContainer>
          {/* Add Item Card */}
          {selectedParameter && (
            <div className={classes.AddChart_Button}>
              <Button
                disabled={cardItems.length >= MAX_ITEM}
                onClick={_addCard}
              >
                Add Pie Item {cardItems.length + 1}
              </Button>
            </div>
          )}

          <div className={classes.Card_Container}>
            {cardItems &&
              cardItems.length > 0 &&
              cardItems.map((cardItem, i) => {
                return (
                  <ItemCard
                    closeCard={() => handleCloseCard(cardItem.id)}
                    defaultItem={cardItem}
                    setColor={(value) => _setCardItemColor(cardItem.id, value)}
                    cardTitle={`Pie Item ${i + 1}`}
                    setSelectValue={(value) =>
                      _setCardItemSelectValue(cardItem.id, value)
                    }
                    setInputValue={(value) =>
                      _setCardItemInputValue(cardItem.id, value)
                    }
                    key={cardItem.id}
                    param={selectedParameter}
                    inputValue={cardItem.label}
                    items={
                      dataFields &&
                      dataFields.find(
                        (parameter) =>
                          parameter.field_name === selectedParameter
                      ) &&
                      dataFields.find(
                        (parameter) =>
                          parameter.field_name === selectedParameter
                      ).field_options
                    }
                  />
                );
              })}
          </div>

          {cardItems && cardItems.length > 0 && (
            <div className={classes.Create_Button}>
              <CreateChartButton
                text={editChart && "Edit"}
                value={chartTitle}
                onChange={(value) => setChartTitle(value)}
                onClick={_createChart}
              />
            </div>
          )}
        </OuterContainer>
      </div>
    </CustomDrawer>
  );
};

export default CreatePieChartDrawer;
