import { Button, Dialog, withStyles } from "@material-ui/core";
import React, { useState } from "react";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import { TextField } from "@material-ui/core";
import Slide from "@material-ui/core/Slide";
import { CustomButton } from "../../../exports";
import Switch from "@material-ui/core/Switch";
import GroupDropDown from "./groupDropdown";
import { useAnalytics } from "../../../../contexts/AnalyticsProvider";

export const groupsPrivilleges = [
  "admin",
  "departments",
  "team heads",
  "team members",
];

const IOSSwitch = withStyles((theme) => ({
  root: {
    width: 42,
    height: 26,
    padding: 0,
    margin: theme.spacing(1),
  },
  switchBase: {
    padding: 1,
    "&$checked": {
      transform: "translateX(16px)",
      color: theme.palette.common.white,
      "& + $track": {
        backgroundColor: "#52d869",
        opacity: 1,
        border: "none",
      },
    },
    "&$focusVisible $thumb": {
      color: "#52d869",
      border: "6px solid #fff",
    },
  },
  thumb: {
    width: 24,
    height: 24,
  },
  track: {
    borderRadius: 26 / 2,
    border: `1px solid ${theme.palette.grey[400]}`,
    backgroundColor: theme.palette.grey[50],
    opacity: 1,
    transition: theme.transitions.create(["background-color", "border"]),
  },
  checked: {},
  focusVisible: {},
}))(({ classes, ...props }) => {
  return (
    <Switch
      focusVisibleClassName={classes.focusVisible}
      disableRipple
      classes={{
        root: classes.root,
        switchBase: classes.switchBase,
        thumb: classes.thumb,
        track: classes.track,
        checked: classes.checked,
      }}
      {...props}
    />
  );
});

const CustomTextField = withStyles({
  root: {
    "& .MuiFilledInput-input": {
      borderRadius: "8px",
    },
    marginBottom: "2% !important",
    "& .MuiFormLabel-root.Mui-error": {
      color: "#f44336",
    },
    "& .MuiFormLabel-root": {
      color: "#8695AC",
    },
    "& .MuiFilledInput-root": {
      backgroundColor: "#F2F3F7",
      borderRadius: "8px",
    },
    "& .MuiFilledInput-underline:before": {
      borderBottomColor: "transparent",
    },
    "& .MuiFilledInput-underline:after": {
      borderBottomColor: "transparent",
    },
    "& .MuiOutlinedInput-root": {
      "& fieldset": {
        borderColor: "red",
      },
      "&:hover fieldset": {
        borderColor: "yellow",
      },
      "&.Mui-focused fieldset": {
        borderColor: "#8695AC",
      },
    },
  },
})(TextField);

const CustomDialog = withStyles((theme) => ({
  root: {
    "& .MuiDialog-paperWidthSm": {
      width: "98%",
      borderRadius: "12px",

      [theme.breakpoints.up("sm")]: {
        width: "100%",
        maxWidth: 400,
      },
    },
  },
}))(Dialog);

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const CreateWorkspaceModal = ({ open, onClose, meta }) => {
  const [workspaceName, setWorkspaceName] = useState(meta ? meta.title : "");
  const [nameError, setNameError] = useState(false);
  const [groupEditPermission, setGroupEditPermission] = useState(
    meta ? meta.allowGroupToEdit : false
  );
  const [group, setGroup] = useState(meta ? meta.visibiltyGroup : "");
  const [groupError, setGroupError] = useState("");
  const { createNewWorkSpace, currentBusiness, editWorkSpaceMeta, userRole } =
    useAnalytics();

  const createWorkx = () => {
    if (!workspaceName) {
      setNameError(true);
      return;
    } else {
      setNameError(false);
    }

    if (!group) {
      setGroupError("please select a group");
      return;
    } else {
      setGroupError("");
    }
    if (meta) {
      onClose();

      return editWorkSpaceMeta({
        groupEditPermission: groupEditPermission,
        group: group,
        title: workspaceName,
        dashboardId: meta._id,
      });
    } else {
      createNewWorkSpace({
        groupEditPermission: groupEditPermission,
        group: group,
        title: workspaceName,
        businessId: currentBusiness,
      });
      onClose();
    }
  };
  return (
    <CustomDialog
      open={open}
      TransitionComponent={Transition}
      keepMounted
      onClose={onClose}
      aria-labelledby="form-dialog-title"
    >
      <DialogTitle id="alert-dialog-slide-title">
        {meta ? "Edit Workspace" : "Create New Workspace"}
      </DialogTitle>

      <DialogContent>
        <CustomTextField
          error={nameError}
          helperText={""}
          value={workspaceName}
          onChange={(e) => setWorkspaceName(e.target.value)}
          fullWidth
          variant="filled"
          label="Name"
        />
        <GroupDropDown
          handleChange={(val) => {
            setGroup(val);
          }}
          list={groupsPrivilleges}
          error={groupError}
          value={group}
        />
        <div className="d-flex align-items-center flex-row-reverse justify-content-end">
          <p>Allow groups to edit</p>
          <IOSSwitch
            disabled={userRole !== "admin"}
            checked={groupEditPermission}
            onChange={(e) => {
              setGroupEditPermission(e.target.checked);
            }}
            name="checkedA"
            color="primary"
            inputProps={{ "aria-label": "primary checkbox" }}
          />
        </div>
        <DialogActions style={{ justifyContent: "flex-start", paddingLeft: 0 }}>
          <CustomButton
            disabled={false}
            style={{
              marginLeft: 0,
              width: "max-content",
              borderRadius: "6px",
              height: "42px",
              fontWeight: "normal",
              padding: "unset",
              paddingLeft: "5px",
              paddingRight: "5px",
              justifyContent: "center",
            }}
            onClick={() => {
              createWorkx();
            }}
          >
            {meta ? "Save" : "Create this workspace"}
          </CustomButton>
          <Button onClick={onClose}>Cancel</Button>
        </DialogActions>
      </DialogContent>
    </CustomDialog>
  );
};

export default CreateWorkspaceModal;
