import { Button, Menu, MenuItem, withStyles } from "@material-ui/core";
import { Backdrop } from "../../../exports";
import WorkspaceListItem from "./WorkspaceListItem/WorkspaceListItem";
import classes from "./WorkspaceModal.module.css";

const CustomMenu = withStyles((theme) => ({
  paper: {
    width: "80%",
    padding: "0",

    [theme.breakpoints.up("sm")]: {
      width: 350,

      top: "62px !important",
    },
  },

  list: {
    paddingBottom: 0,
  },
}))(Menu);

const WorkspaceModal = ({
  open,
  handleClose,
  anchorEl,
  currentWorkspaceId,
  workspaces,
  dashboardDropDownChange,
  createNewWorkx,
  onEdit,
  onDelete,
}) => {
  return (
    <>
      <Backdrop show={open} clicked={handleClose}></Backdrop>

      <CustomMenu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={open}
        className={classes.Menu}
        onClose={handleClose}
      >
        {workspaces.map((workspace, i) => {
          const { title, _id } = workspace;
          return (
            <MenuItem key={_id} button={false} className={classes.MenuItem}>
              <WorkspaceListItem
                onClick={() => {
                  dashboardDropDownChange(workspaces, title);

                  handleClose();
                }}
                onEdit={() => onEdit(workspace)}
                onDelete={() => onDelete(_id)}
                key={_id}
                title={title}
                active={Boolean(_id === currentWorkspaceId)}
              />
            </MenuItem>
          );
        })}

        <div className={classes.Modal_Bottom}>
          <Button onClick={createNewWorkx}>Create New Workspace</Button>
        </div>
      </CustomMenu>
    </>
  );
};

export default WorkspaceModal;
