import { Hidden } from "@material-ui/core";
import SidebarDesktop from "../../Sidebar/Sidebar";
import { NavbarContainer } from "../../../shared-components/mobile";

const AnalyticsSidebar = ({
  allBusiness,
  open,
  location,
  toggleBar,
  selectedBusiness,
  className,
  switchBusiness,
  isLoading,
}) => {
  return (
    <>
      {/* Mobile Only */}
      <NavbarContainer
        switchBusiness={switchBusiness}
        open={open}
        toggleBar={toggleBar}
        isLoading={isLoading}
      />

      {/* Desktop Only */}
      <Hidden xsDown implementation="js">
        <div style={{ display: "flex", alignItems: "center" }}>
          <SidebarDesktop
            className={className}
            selectedBusiness={selectedBusiness}
            business={allBusiness}
            open={open}
            location={location}
            switchBetweenBusiness={switchBusiness}
            handleClose={toggleBar}
          />
        </div>
      </Hidden>
    </>
  );
};

export default AnalyticsSidebar;
