import { Button } from "@material-ui/core";
import { Close } from "@material-ui/icons";
import Check from "@material-ui/icons/Check";
import classes from "./WorkspaceListItem.module.css";

const Edit = ({ style }) => (
  <svg
    style={style}
    width="18"
    height="18"
    viewBox="0 0 18 18"
    fill="none"
    xmlns="http://www.w3.org/2000/svg"
    xmlnsXlink="http://www.w3.org/1999/xlink"
  >
    <rect opacity="0.7" width="18" height="18" fill="url(#pattern0)" />
    <defs>
      <pattern
        id="pattern0"
        patternContentUnits="objectBoundingBox"
        width="1"
        height="1"
      >
        <use xlinkHref="#image0" transform="scale(0.005)" />
      </pattern>
      <image
        id="image0"
        width="200"
        height="200"
        xlinkHref="data:image/png;base64,"
      />
    </defs>
  </svg>
);

const WorkspaceListItem = ({ title, active, onClick, onEdit, onDelete }) => {
  return (
    <>
      <div className={classes.MenuItem_Text}>
        <Check
          classes={{ root: !active && classes.Hide }}
          style={{ color: "#12B0EE" }}
        />
        <Button onClick={onClick}>{title}</Button>
      </div>
      <div className={classes.MenuItem_buttons}>
        <Button className={classes.Button} onClick={onEdit}>
          <Edit style={{ width: "27px", height: "27px" }} />
        </Button>

        <Button className={classes.Button} onClick={() => onDelete()}>
          <Close />
        </Button>
      </div>
    </>
  );
};

export default WorkspaceListItem;
