import { Button, Divider, Hidden } from "@material-ui/core";
import { motion } from "framer-motion";
import { useCallback, useEffect, useRef, useState } from "react";

// Redux
import {
  fetchDefaultBusiness,
  fetchUserDetail,
  getUserBusiness,
} from "../../Store/actions";
import { connect } from "react-redux";

// Styles
import classes from "./Analytics.module.css";

// Componants
import AnalyticsSidebar from "../../Components/Analytics/AnalyticsSidebar/AnalyticsSidebar";
import NavigationBar from "../../Components/Analytics/NavigationBar/NavigationBar";
import NoCharts from "../../Components/Analytics/NoCharts/NoCharts";
import { useAnalytics } from "../../contexts/AnalyticsProvider";
import GridContainer from "../../Container/Analytics/GridContainer/GridContainer";
import Drawers from "../../Container/Analytics/Drawers/Drawers";
// import GridContainer from "../../Container/Analytics/GridContainer/GridContainer";

import "./index.css";
import { Loader } from "../../Components/exports";
import { useBusiness } from "../../contexts/BusinessProvider";
import BottomNavigation from "../../shared-components/mobile/BottomNavigation/BottomNavigation";
import { isEmptyObject } from "../../Shared/Utility";
import { Add, EditOutlined } from "@material-ui/icons";

const NoteContainer = ({ text }) => {
  return (
    <div className={classes.NoteScreen}>
      <h1>{text}</h1>
    </div>
  );
};

const Analytics = ({
  allBusiness,
  selectedBusiness,
  history,
  location,
  match,
  ...props
}) => {
  const {
    currentWorkspace,
    dataFields,
    state,
    setState,
    currentBusiness,
    leads,
    getLeadDataFields,
    _currentBusiness,
    updateLayout,
    editChart,
    workspaceData,
    isLoading,
    noteConfig,
    currentWorkspaceData,
    checkDepartment,
    openAddChartDrawer,
    userRole,
    userRoleFilterData,
  } = useAnalytics();

  const { switchBusiness } = useBusiness();

  const { showMessageScreen, noChart, editInstance } = state;

  const [states, setStates] = useState({
    toggled: location.hash === "#open" ? true : false,
    selectedLead: null,
    toggle_DropDown: false,
  });

  const switchBetweenBusiness = (e) => {
    if (selectedBusiness.id === e.target.value) {
      return;
    }

    const business = allBusiness.find(
      (data) => data.businessId === e.target.value
    );

    if (business) {
      switchBusiness(e.target.value, business);

      window.location.href = "/analytics";
    }
  };

  const toggleBar = () => {
    if (states.toggled) {
      history.push("#close");
    }
    if (!states.toggled) {
      history.push("#open");
    }
    return setStates({ ...states, toggled: !states.toggled });
  };

  const loadDataRef = useRef(() => {
    !selectedBusiness && props.fetchDefaultBusiness();
    allBusiness.length < 1 && props.getAllUserBusiness();
    getLeadDataFields();
  });

  useEffect(() => {
    loadDataRef.current();
  }, []);

  const dashboardId = match.params.dashboardId;

  useEffect(() => {
    if (selectedBusiness && !currentBusiness) {
      checkDepartment(
        selectedBusiness.id.substr(0, selectedBusiness.id.length - 2),
        dashboardId
      );
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedBusiness, currentBusiness]);

  const createItemObj = useCallback(() => {
    if (!currentWorkspaceData) return;

    let obj = {};

    currentWorkspaceData.charts.forEach((ch, idx) => {
      obj[ch.itemKey] = ch;
    });

    currentWorkspaceData.feeds.forEach((f, idx) => {
      obj[f.itemKey] = f;
    });

    currentWorkspaceData.tables.forEach((t, idx) => {
      obj[t.itemKey] = t;
    });

    if (isEmptyObject(obj)) {
      return setState((prevState) => ({ ...prevState, noChart: true }));
    } else {
      return setState((prevState) => ({ ...prevState, noChart: false }));
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentWorkspaceData]);

  useEffect(() => {
    createItemObj();
  }, [createItemObj]);

  return (
    <motion.div className={classes.Container}>
      <div className={classes.inner}>
        <AnalyticsSidebar
          allBusiness={allBusiness}
          selectedBusiness={selectedBusiness}
          toggleBar={toggleBar}
          open={states.toggled}
          className={classes.Sidebar}
          location={location}
          isLoading={isLoading}
          switchBusiness={switchBetweenBusiness}
        />
        <div className={classes.AnalyticsContent}>
          <NavigationBar />

          {/* Divider */}
          <Hidden xsDown implementation="js">
            <Divider style={{ width: "95%", margin: "0 auto 10px" }} />
          </Hidden>

          {/* {!state.isFLayout && <NoCharts />} */}
          {showMessageScreen && !isLoading && !noChart && (
            <NoteContainer
              text={
                noteConfig.message ||
                (userRole &&
                  userRole === "admin" &&
                  userRoleFilterData.length > 0 &&
                  "Please select user from dropdown!")
              }
            />
          )}

          {!isLoading && noChart && (
            <NoCharts
              disabled={isLoading || editInstance}
              openAddChartDrawer={openAddChartDrawer}
            />
          )}
          {isLoading && (
            <Loader className={classes.Loader} style={{ zIndex: 1 }} />
          )}

          {!noChart &&
            currentWorkspace &&
            !showMessageScreen &&
            dataFields &&
            !isLoading &&
            workspaceData &&
            currentWorkspaceData && (
              <div className={classes.Grid_Container}>
                {/* Only visible for mobile users */}
                <div className={classes.Control_Buttons}>
                  <Button
                    disabled={isLoading || state.showMessageScreen}
                    onClick={() => {
                      if (isLoading || state.showMessageScreen) return;

                      setState((prevState) => ({
                        ...prevState,
                        editInstance: !prevState.editInstance,
                      }));
                    }}
                    className={classes.Add}
                  >
                    <EditOutlined style={{ color: "#24272C", opacity: 0.7 }} />
                  </Button>
                  <Button
                    disabled={isLoading || editInstance || showMessageScreen}
                    onClick={openAddChartDrawer}
                    className={classes.Add}
                  >
                    <Add style={{ color: "#12B0EE" }} />
                  </Button>
                </div>

                <GridContainer
                  instance={currentWorkspaceData}
                  editInstance={editInstance}
                  leads={leads}
                  businessData={_currentBusiness(currentBusiness, allBusiness)}
                  updateLayout={(layout) => updateLayout(layout)}
                  editChart={(chart) => editChart(chart)}
                  workspaceData={workspaceData}
                />
              </div>
            )}
        </div>
      </div>
      <Hidden implementation="js" smUp>
        <BottomNavigation />
      </Hidden>
      <Drawers />
    </motion.div>
  );
};

const mapStateToProps = (state) => {
  return {
    allBusiness: state.business.allBusiness,
    selectedBusiness: state.business.selectedBusiness,
    user: state.user.user,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getAllUserBusiness: () => dispatch(getUserBusiness()),
    fetchDefaultBusiness: () => dispatch(fetchDefaultBusiness()),
    fetchUserDetail: () => dispatch(fetchUserDetail()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Analytics);
