import { Button, TextField, withStyles } from "@material-ui/core";
import { ArrowDropDown } from "@material-ui/icons";
import React from "react";
import { useAnalytics } from "../../../contexts/AnalyticsProvider";
import classes from "./NavigationBar.module.css";
import CreateWorkspaceModal from "./WorkSpaceCreateModal/createWorkspace";
import WorkspaceModal from "./WorkspaceModal/WorkspaceModal";

import { useEffect } from "react";
import Autocomplete from "@material-ui/lab/Autocomplete";
import { withRouter } from "react-router-dom/cjs/react-router-dom.min";
import { useState } from "react";

const CustomInput = withStyles(() => ({
  root: {
    "& .MuiInputBase-root": {
      padding: "5px 5px 5px 5px",
    },

    "& .MuiOutlinedInput-notchedOutline": {
      border: "unset",
    },
  },
}))(TextField);

const NavigationBar = ({ location, history, match }) => {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [isCreateModal, setIsCreateModal] = React.useState(false);
  const [meta, setMeta] = React.useState(undefined);
  const [autoCompleteValue, setAutoCompleteValue] = useState("");

  const {
    currentWorkspace,
    workspaces,
    setCurrentWorkspace,
    getWorkspaceData,
    currentBusiness,
    openAddChartDrawer,
    getUersRoleBid,
    userRoleFilterData,
    setUserRoleFilterData,
    deleteWorkspace,
    shouldCached,
    state,
    setState,
    setWorkspaceData,
    getWorkspaceById,
    currentWorkspaceData,
    setMemberUserId,
    userRole,
    isLoading,
  } = useAnalytics();

  const { editInstance, noChart } = state;

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  useEffect(() => {
    if (currentWorkspace && workspaces && workspaces.length > 0 && userRole) {
      const dashboard = workspaces.find(
        (workspace) => workspace._id === currentWorkspace
      );

      if (dashboard) {
        setUserRoleFilterData([]);
        // empty autocomplete input when switching between dashboards

        setAutoCompleteValue("");

        setState({ ...state, noChart: false });

        if (dashboard.visibiltyGroup === "admin") {
          setMemberUserId(null);
          setWorkspaceData(undefined);

          getWorkspaceData(
            currentBusiness,
            dashboard._id,
            shouldCached,
            dashboard.visibiltyGroup.replace(" ", "").toLowerCase(),
            ""
          );

          setState({ ...state, showMessageScreen: false });
        } else if (
          dashboard.visibiltyGroup !== "admin" &&
          userRole !== "admin"
        ) {
          setWorkspaceData(undefined);

          getWorkspaceData(
            currentBusiness,
            dashboard._id,
            shouldCached,
            dashboard.visibiltyGroup.replace(" ", "").toLowerCase(),
            localStorage.getItem("userId")
          );

          setState({ ...state, showMessageScreen: true });
        }

        if (dashboard.visibiltyGroup !== "admin" && userRole === "admin") {
          getUersRoleBid(
            currentBusiness,
            dashboard.visibiltyGroup.replace(" ", "").toLowerCase()
          );
        }
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [workspaces, currentWorkspace, currentBusiness, userRole]);

  useEffect(() => {
    if (
      currentWorkspace &&
      workspaces.length > 0 &&
      currentWorkspaceData &&
      userRoleFilterData.length > 0
    ) {
      setWorkspaceData(undefined);

      getWorkspaceData(
        currentBusiness,
        currentWorkspaceData._id,
        shouldCached,
        currentWorkspaceData.visibiltyGroup.replace(" ", "").toLowerCase(),
        ""
      );

      setState({ ...state, showMessageScreen: true });
    }
  }, [userRoleFilterData]);

  const defaultDropDown = (array, current) => {
    if (!current || !array) {
      return "Select Workspace";
    }
    for (let index = 0; index < array.length; index++) {
      if (array[index]._id === current) {
        return array[index].title;
      }
    }
    return "Select Workspace";
  };

  const _dashboardDropDownChange = (array, value) => {
    for (let index = 0; index < array.length; index++) {
      if (array[index].title === value) {
        // clear all current workspace data
        setWorkspaceData(undefined);
        setMemberUserId(null);

        history.push(`/engage/analytics/${array[index]._id}`);

        setCurrentWorkspace(array[index]._id);

        getWorkspaceById({ id: array[index]._id });
        // setcurrentWorkspaceData(array[index]);

        break;
      }
    }
  };

  const _createNewWorkspace = () => {
    setIsCreateModal(!isCreateModal);
    handleClose();
  };

  return (
    <div className={classes.Container}>
      {/* handle Workspace */}

      <div className={classes.Flex}>
        <div className={classes.Workspace_Container}>
          <Button onClick={handleClick}>
            {defaultDropDown(workspaces, match.params.dashboardId)}
            <ArrowDropDown />
          </Button>
          {workspaces && (
            <WorkspaceModal
              dashboardDropDownChange={_dashboardDropDownChange}
              currentWorkspaceId={currentWorkspace}
              workspaces={workspaces}
              open={Boolean(anchorEl)}
              anchorEl={anchorEl}
              handleClose={handleClose}
              createNewWorkx={_createNewWorkspace}
              onEdit={(did) => {
                setMeta(did);
                setIsCreateModal(true);
              }}
              onDelete={(did) => {
                deleteWorkspace(did);
              }}
            />
          )}
        </div>
        {!noChart &&
          userRole &&
          userRole === "admin" &&
          currentWorkspaceData &&
          currentWorkspaceData.visibiltyGroup !== "admin" && (
            //  <SearchDropDown title={currentWorkspaceData.visibiltyGroup} list={userRoleFilterData} handleChange={() => { }} />
            <Autocomplete
              id="role-users-list"
              options={userRoleFilterData}
              getOptionLabel={(option) => option.value}
              className={classes.AutoComplete_Input}
              blurOnSelect={true}
              inputValue={autoCompleteValue || ""}
              onChange={(event, option) => {
                if (option && option.key) {
                  getWorkspaceData(
                    currentBusiness,
                    currentWorkspaceData._id,
                    shouldCached,
                    currentWorkspaceData.visibiltyGroup
                      .replace(" ", "")
                      .toLowerCase(),
                    option.key
                  );

                  setMemberUserId(option.key);
                  setAutoCompleteValue(option.value);
                }
              }}
              renderInput={(params) => {
                return (
                  <CustomInput
                    {...params}
                    value={autoCompleteValue}
                    placeholder={currentWorkspaceData.visibiltyGroup}
                    variant="outlined"
                    type="text"
                  />
                );
              }}
            />
          )}
      </div>

      {(userRole === "admin" ||
        (currentWorkspaceData && currentWorkspaceData.allowGroupToEdit)) && (
        <div className={classes.Action_Buttons}>
          <div className={classes.EditChart_Button}>
            <Button
              disabled={isLoading || state.showMessageScreen || noChart}
              onClick={() => {
                if (isLoading || state.showMessageScreen) return;

                setState((prevState) => ({
                  ...prevState,
                  editInstance: !prevState.editInstance,
                  editChart: false,
                  editParams: undefined,
                }));
              }}
            >
              {(!editInstance && "Toggle Edit Mode") || "Cancel Edit Mode"}
            </Button>
          </div>

          <div className={classes.AddChart_Button}>
            <Button
              disabled={
                isLoading || state.editInstance || state.showMessageScreen
              }
              onClick={openAddChartDrawer}
            >
              Add a New Chart
            </Button>
          </div>
        </div>
      )}

      {isCreateModal && (
        <>
          <CreateWorkspaceModal
            open={isCreateModal}
            onClose={() => {
              setIsCreateModal(false);
              setMeta(undefined);
            }}
            meta={meta}
          />
        </>
      )}
    </div>
  );
};

export default withRouter(NavigationBar);
