import {
  Card,
  CardContent,
  FormControl,
  IconButton,
  InputLabel,
  MenuItem,
  Select,
  withStyles,
} from "@material-ui/core";
import { Close } from "@material-ui/icons";
import React, { useState } from "react";
import { colors } from "../../../Constants/Analytics";
import { CustomTextFieldFilled } from "../../UI/CustomTextField/CustomTextField";
import classes from "./ItemCard.module.css";

import PropTypes from "prop-types";

const CustomSelect = withStyles(() => ({
  root: {
    backgroundColor: "#f2f3f7",
    borderRadius: 8,
  },
}))(Select);

export const ColorPicker = (colorName, toLowerCase = false) => {
  // let status = ["Cyan", "Magneto", "Krato", "Magento", "Blue", "Lemon"];

  // let colors = [
  //   "#41AFA3",
  //   "#C85B70",
  //   "#FFBA43",
  //   "#D786EB",
  //   "#6293DC",
  //   "#DADC62",
  // ];

  const res = colors.findIndex((e) => e.label === colorName);

  return colors[res].color || "#41AFA3";
};

const CardHeader = ({
  currentActiveColor,
  setCurrentActiveColor,
  cardTitle,
  setColor,
  closeCard,
}) => {
  return (
    <div className={classes.Header}>
      <h1 variant="h1" component="h1">
        {cardTitle || "Card Title"}
      </h1>

      <div className={classes.Card_Control}>
        <div className={classes.Color_Controller}>
          <Select
            value={currentActiveColor}
            className={classes.selectEmpty}
            inputProps={{ "aria-label": "Without label" }}
            onChange={(e) => {
              setCurrentActiveColor(e.target.value);
              setColor(ColorPicker(e.target.value));
            }}
          >
            {colors.map(({ label, index, value, color }, i) => {
              return (
                <MenuItem
                  value={label}
                  key={index}
                  classes={{ root: classes.MenuList }}
                >
                  <div
                    className={classes.Lead_Color}
                    style={{
                      backgroundColor: color || "black",
                    }}
                  ></div>
                  <p
                    style={{
                      color: color,
                    }}
                  >
                    {label}
                  </p>
                </MenuItem>
              );
            })}
          </Select>
        </div>
        <IconButton onClick={closeCard}>
          <Close />
        </IconButton>
      </div>
    </div>
  );
};

const ItemCard = ({
  cardTitle,
  param,
  items,
  setSelectValue,
  setInputValue,
  setColor,
  defaultItem,
  closeCard,
  inputValue,
  onClick,
}) => {
  const [error, setError] = useState(false);

  const [currentActiveColor, setCurrentActiveColor] = useState(
    colors.find((e) => e.color === defaultItem.color).label || "Cyan"
  );

  const key = defaultItem.id;

  return (
    <Card
      key={defaultItem.id}
      onClick={onClick}
      variant="outlined"
      className={classes.ItemCard}
    >
      <CardContent>
        <CardHeader
          closeCard={closeCard}
          cardTitle={cardTitle}
          setCurrentActiveColor={setCurrentActiveColor}
          currentActiveColor={currentActiveColor}
          setColor={setColor}
        />

        {/* Select  Item*/}
        <FormControl variant="filled" className={classes.ItemSelect}>
          <InputLabel id="demo-simple-select-filled-label">
            Select {param}
          </InputLabel>
          <CustomSelect
            labelId="demo-simple-select-filled-label"
            id="demo-simple-select-filled"
            onChange={(e) => {
              setSelectValue(e.target.value);

              // Set label value
              setInputValue(e.target.value);
            }}
            className={classes.CustomSelect}
            defaultValue={(defaultItem && defaultItem.item) || ""}
            key={`drop_down${key}`}
          >
            <MenuItem value="">
              <em>None</em>
            </MenuItem>
            {items &&
              items.map((item, i) => (
                <MenuItem key={i} value={item}>
                  {item}
                </MenuItem>
              ))}
          </CustomSelect>
        </FormControl>

        {/* Label value */}
        <CustomTextFieldFilled
          label="Label"
          required
          className={classes.Input}
          onChange={(e) => {
            if (e.target.value.length < 1) {
              setError(true);
            } else {
              setError(false);
            }

            setInputValue(e.target.value);
          }}
          onClick={(e) => {
            if (defaultItem.label.length < 1) {
              setError(true);
            } else {
              setError(false);
            }
          }}
          defaultValue={inputValue || ""}
          key={`input_field${key}${inputValue}`}
          error={error}
          helperText={error && "required"}
        />
      </CardContent>
    </Card>
  );
};

ItemCard.propTypes = {
  cardTitle: PropTypes.string.isRequired,
  param: PropTypes.string.isRequired,
  items: PropTypes.array.isRequired,
  setSelectValue: PropTypes.func.isRequired,
  setInputValue: PropTypes.func.isRequired,
  setColor: PropTypes.func.isRequired,
  defaultItem: PropTypes.object.isRequired,
  closeCard: PropTypes.func.isRequired,
  inputValue: PropTypes.string.isRequired,
};

export default ItemCard;
