import clsx from "clsx";
import React, { memo } from "react";
// import { Line } from "react-chartjs-2";
import classes from "./LineChart.module.css";
import "./styles/lineChart.css";

import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts";
import EditControl from "../../EditControl/EditControl";
import { useAnalytics } from "../../../../contexts/AnalyticsProvider";

const LineChartContainer = ({
  chartData,
  title = "Detailed Chart",
  period = 90,
  deleteChart = undefined,
  editChart,
  demoChart = false,
}) => {
  const { state, setDrawers } = useAnalytics();

  const { editInstance } = state;

  return (
    <div className={clsx("lineChart", classes.Container)}>
      {!demoChart && editInstance && (
        <EditControl
          openDrawer={() =>
            setDrawers((prevState) => ({
              ...prevState,
              openCreateLineChartDrawer: true,
            }))
          }
          editChart={editChart}
          deleteChart={deleteChart}
        />
      )}

      <div
        style={{
          height: editInstance && deleteChart ? "calc(100% - 32px)" : "100%",
        }}
        className={classes.Inner}
      >
        {/* Header of Line chart */}
        {(title || period) && (
          <div className={classes.Chart_Title}>
            <h1>{title}</h1>
            <p>{period === 0 ? "Today" : `${period} days`} </p>
          </div>
        )}
        <div className={classes.Chart_Container}>
          <ResponsiveContainer>
            <LineChart
              data={chartData.datasets.data}
              margin={{
                top: 20,
                right: 5,
                left: 5,
                bottom: -25,
              }}
              className={classes.Chart}
            >
              <CartesianGrid strokeDasharray="4 4" strokeOpacity={0.3} />
              <Legend
                className={classes.Legend_Wrapper}
                verticalAlign="top"
                align="right"
                iconSize={8}
              />

              <XAxis
                allowDuplicatedCategory={false}
                opacity={0.3}
                dataKey="date"
                tick={false}
              />
              <YAxis mirror opacity={0.3} fontSize={12} />
              <Tooltip />
              {chartData.chartLabels &&
                chartData.chartLabels.map((label, i) => {
                  return (
                    <Line
                      radius={10}
                      strokeDasharray={label.comparison && "5, 5"}
                      key={i}
                      type={label.comparison ? "linear" : "monotone"}
                      dataKey={label.y}
                      stroke={label.comparison ? "#32c5ff" : label.borderColor}
                      activeDot={{ r: 5 }}
                      legendType="circle"
                    />
                  );
                })}
            </LineChart>
          </ResponsiveContainer>
        </div>
      </div>
    </div>
  );
};

export default memo(LineChartContainer);
