import React, { useCallback, useEffect, useState } from "react";
import AddChartDrawer from "../../../Components/Analytics/AddChartDrawer/AddChartDrawer";
import CreateDisplayFeedDrawer from "../../../Components/Analytics/CreateChartDrawers/CreateDisplayFeedDrawer/CreateDisplayFeedDrawer.jsx";
import CreateLineChartDrawer from "../../../Components/Analytics/CreateChartDrawers/CreateLineChartDrawer/CreateLineChartDrawer.jsx";
import CreatePerformanceTableDrawer from "../../../Components/Analytics/CreateChartDrawers/CreatePerformanceTableDrawer/CreatePerformanceTableDrawer.jsx";
import CreatePieChartDrawer from "../../../Components/Analytics/CreateChartDrawers/CreatePieChartDrawer/CreatePieChartDrawer.jsx";
import { useAnalytics } from "../../../contexts/AnalyticsProvider";

const Drawers = () => {
  const { drawers, setDrawers } = useAnalytics();

  const [keepDrawerMounted, setKeepDrawerMounted] = useState(false);

  const {
    openAddChartDrawer,
    openCreateLineChartDrawer,
    openCreatePieChartDrawer,
    openCreateDisplayFeedDrawer,
    openCreatePerformanceTableDrawer,
  } = drawers;

  const handleDrawerToggle = (drawer) => {
    setKeepDrawerMounted(false);

    return setDrawers((prevState) => ({
      ...prevState,
      [drawer]: false,
      openAddChartDrawer: false,
    }));
  };

  const keepMounted = useCallback(() => {
    if (openAddChartDrawer) {
      return setKeepDrawerMounted(true);
    } else {
      return setKeepDrawerMounted(false);
    }
  }, [openAddChartDrawer]);

  useEffect(() => {
    keepMounted();
  }, [keepMounted]);

  return (
    <>
      <AddChartDrawer
        open={openAddChartDrawer}
        handleDrawerToggle={() => handleDrawerToggle("openAddChartDrawer")}
      />

      <CreateLineChartDrawer
        keepMounted={keepDrawerMounted}
        open={openCreateLineChartDrawer}
        handleDrawerToggle={() =>
          handleDrawerToggle("openCreateLineChartDrawer")
        }
      />

      <CreatePieChartDrawer
        open={openCreatePieChartDrawer}
        keepMounted={keepDrawerMounted}
        handleDrawerToggle={() =>
          handleDrawerToggle("openCreatePieChartDrawer")
        }
      />

      <CreateDisplayFeedDrawer
        open={openCreateDisplayFeedDrawer}
        keepMounted={keepDrawerMounted}
        handleDrawerToggle={() =>
          handleDrawerToggle("openCreateDisplayFeedDrawer")
        }
      />

      <CreatePerformanceTableDrawer
        open={openCreatePerformanceTableDrawer}
        keepMounted={keepDrawerMounted}
        handleDrawerToggle={() =>
          handleDrawerToggle("openCreatePerformanceTableDrawer")
        }
      />
    </>
  );
};

export default Drawers;
